    private AdaptPhone() {
        // iPhone刘海适配
        if (cc.sys.os == cc.sys.OS_IOS) {
            let screen = cc.view.getFrameSize();
            let screenRate = screen.height / screen.width;
            if (screenRate >= 1.99) {
                this.ContentPanel.top = 50;
            }
        }
    }
